.floatingText {
  position: fixed;
  bottom: 140px;
  left: 50%;
  transform: translateX(-50%);
  font-family: 'Playfair Display', 'Times New Roman', serif;
  font-size: 64px;
  font-weight: 300;
  color: rgba(0, 0, 0, 0.3);
  white-space: nowrap;
  z-index: 1;
  pointer-events: none;
  letter-spacing: 0.05em;
  display: flex;
  align-items: center;
  width: 100%;
}

.textContainer {
  width: 100%;
  padding: 0 20px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.desktopText {
  display: block;
}

.mobileText {
  display: none;
}

.mobileLine {
  display: block;
  text-align: center;
}

.char {
  display: inline-block;
  color: rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  animation: charAppear 0.8s ease-out forwards;
}

.hidden {
  opacity: 0;
  transform: translateY(20px) scale(0.8);
  filter: blur(20px);
}

.visible {
  opacity: 0.3;
  transform: translateY(0px) scale(1);
  filter: blur(0px);
  animation: charFloat 8s ease-in-out infinite;
}

@keyframes charAppear {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.8);
    filter: blur(25px);
  }
  30% {
    opacity: 0.1;
    transform: translateY(10px) scale(0.9);
    filter: blur(15px);
  }
  60% {
    opacity: 0.2;
    transform: translateY(-3px) scale(1.02);
    filter: blur(5px);
  }
  100% {
    opacity: 0.3;
    transform: translateY(0px) scale(1);
    filter: blur(0px);
  }
}

@keyframes charFloat {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
    filter: blur(0px);
  }
  25% {
    transform: translateY(-8px) rotate(1.2deg);
    filter: blur(2px);
  }
  50% {
    transform: translateY(0px) rotate(0deg);
    filter: blur(0px);
  }
  75% {
    transform: translateY(6px) rotate(-1deg);
    filter: blur(1.5px);
  }
}


/* Responsive design */
@media (max-width: 480px) {
  .floatingText {
    position: relative !important;
    bottom: auto !important;
    top: auto !important;
    left: auto !important;
    transform: none !important;
    font-size: 32px !important; /* Increased from 28px */
    letter-spacing: 0.02em !important;
    margin: 16px 0 4px 0 !important;
    padding: 0 !important; 
    text-align: center !important;
    line-height: 1.2 !important;
    width: 100% !important;
    color: rgba(0, 0, 0, 0.8) !important; /* Increased opacity from 0.7 */
  }
  
  .textContainer {
    padding: 0 20px !important;
    width: 100% !important; 
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
    flex-direction: column !important; 
    text-align: center !important; 
  }
  
  .desktopText {
    display: none !important;
  }
  
  .mobileText {
    display: block !important;
  }
  
  .mobileLine {
    margin-bottom: 4px !important;
  }
  
  .char {
    animation: charAppear 0.6s ease-out forwards !important; /* Faster from 0.8s */
  }
  
  .visible {
    animation: charFloat 6s ease-in-out infinite !important; /* Faster from 8s */
  }
}

